import Vue from 'vue'
import Vuex from 'vuex'
import localforage from 'localforage'


Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    logIn: false,
    token: null,
    role: null
  },
  mutations: {
    login_method: function (state, data) {
      state.logIn = true;
      state.token = data.token;
      state.role = data.role;
      localforage.setItem("token",data.token).catch(msg =>{
        console.log(msg);
      });
      localforage.setItem("role",data.role).catch(msg =>{
        console.log(msg)
      })
    },
    logout_method: function (state) {
      state.logIn = false;
      state.token = null;
      state.role = null;
      localforage.removeItem('token').catch(error =>{
        console.log(error)
      });
      localforage.removeItem('role').catch(error =>{
        console.log(error)
      })
    }
  },
  actions: {
  },
  modules: {
  }
})
