<template>
  <v-app>
    <v-navigation-drawer
        dark
        v-model="drawer"
        fixed
        app
        touchless
        disable-resize-watcher
        width="180"
        src="https://cdn.vuetifyjs.com/images/backgrounds/bg-2.jpg"
    >
        <v-list
          nav
          class="py-0"
        >
          <v-list-item two-line>
            <v-list-item-avatar>
              <img src="https://randomuser.me/api/portraits/men/81.jpg">
            </v-list-item-avatar>

            <v-list-item-content>
              <v-list-item-title>{{ name }}</v-list-item-title>
              <v-list-item-subtitle>{{ title }}</v-list-item-subtitle>
            </v-list-item-content>
          </v-list-item>

          <v-divider></v-divider>

          <v-list-item
            v-for="item in items"
            :key="item.title"
            link
          >
            <v-list-item-icon>
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-item-icon>

            <v-list-item-content>
              <v-list-item-title>{{ item.title }}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list>
      <template v-slot:append>
        <div class="pa-6">
          <v-btn color="grey darken-1" block @click="logOut">退出登录</v-btn>
        </div>
      </template>
    </v-navigation-drawer>
    <v-app-bar
                app
                color="blue darken-3"
                dark
                elevate-on-scroll
                src="https://cdn.vuetifyjs.com/images/backgrounds/vbanner.jpg"
                @click="logIn ? drawer = !drawer : clickFn()"
        >
            <div class="d-flex align-center">
                <v-img
                        alt="Vuetify Logo"
                        class="shrink mr-2"
                        contain
                        src="https://cdn.vuetifyjs.com/images/logos/vuetify-logo-dark.png"
                        transition="scale-transition"
                        width="40"
                />

                <v-img
                        alt="Vuetify Name"
                        class="shrink mt-1 hidden-sm-and-down"
                        contain
                        min-width="100"
                        src="https://cdn.vuetifyjs.com/images/logos/vuetify-name-dark.png"
                        width="100"
                />
            </div>
            <v-spacer></v-spacer>
            <span class="mr-2" v-if="logIn">{{ name }}</span>
            <v-btn
                    icon
                    large
                    v-if="logIn"
            >
                <v-avatar
                        size="42px"
                        item
                        color="grey lighten-3"
                        @click.stop="drawer = !drawer"
                >
                    <v-img
                            src="./assets/worker.png"
                            alt="Vuetify"
                    />
                </v-avatar>
            </v-btn>
            <v-btn
                    icon
                    large
                    v-else
            >
                <v-avatar
                        size="42px"
                        item
                        @click.stop="clickFn()"
                >
                    <v-icon large>mdi-account-circle</v-icon>
                </v-avatar>
            </v-btn>
        </v-app-bar>
    <v-content style="margin-bottom: 48px">
      <v-container fluid>
        <router-view></router-view>
      </v-container>
    </v-content>
    <v-bottom-navigation
    fixed
    grow
  >
    <v-btn :to="homeLink" style="height: 56px">
      <v-icon medium>mdi-home</v-icon>
    </v-btn>

    <v-btn :to="appsLink" style="height: 56px">
      <v-icon medium>mdi-apps</v-icon>
    </v-btn>

    <v-btn @click="goBack" style="height: 56px">
      <v-icon medium>mdi-arrow-left</v-icon>
    </v-btn>
  </v-bottom-navigation>
  </v-app>
</template>

<script>
import localforage from 'localforage'
export default {
  name: 'App',
  data: () => ({
    drawer: false,
    items: [
        {title: 'Dashboard', icon: 'mdi-view-dashboard'},
        {title: 'Photos', icon: 'mdi-image'},
        {title: 'About', icon: 'mdi-help-box'},
    ],
    name:'张三',
    title: '主操',
    homeLink:'/',
    appsLink:'/task'
  }),
    methods:{
        goBack: function () {
        this.$router.go(-1)
        },
        clickFn: function f() {
        this.$router.push({name: 'Login.vue'})
        },
        logOut: function () {
            this.$store.commit('logout_method');
            this.drawer = false
        }
        },
    computed: {
      logIn: function () {
          return this.$store.state.logIn
      }
    },
    created() {
      localforage.getItem('token').then(value => {
          if (value) {this.$store.state.logIn = true;
          this.$store.state.token = value}
      }).catch(error => {
          console.log(error)
      });
      localforage.getItem('role').then(value => {
          if (value) {
              this.$store.state.role = value
          }
      }).catch(error =>{
          console.log(error)
      })
    }
}
</script>

